}
.about {
  margin-top: 6rem;
}

.intoduction span {
  color: #00BFFF;
  font-family: 'SF Mono', 'Fira Code', 'Fira Mono', 'Roboto Mono', monospace;
}

.intoduction h2 {
  color: #ccd6f6;
  font-weight: 600;
  font-size: 5rem;
  margin: 2rem 0;
}

.intoduction p {
  color: #8892b0;
  font-size: 16px;
  width: 50%;
  display: block;
  margin-bottom: 1rem;
}

p {
  transform: translate(0px, 0px);
  opacity: 1;
  font-family: SF Pro Display, sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 1.6rem;
  line-height: 1.9rem;
  margin-bottom: 3.2rem;
}

.about .learnmore {
  width: 180px;
  display: block;
  color: #00BFFF;
  background-color: transparent;
  border: 1px solid #00BFFF;
  border-radius: 4px;
  padding: 1.25rem 1.75rem;
  font-size: 14px;
  font-family: 'SF Mono', 'Fira Code', 'Fira Mono', 'Roboto Mono', monospace;
  line-height: 1;
  text-decoration: none;
  transition: all 0.25s cubic-bezier(0.645, 0.045, 0.355, 1);
  margin: 4rem 0;
}

.about .learnmore:hover {
  outline: none;
  text-decoration: none;
  cursor: pointer;
  box-shadow: 4px 4px 0 0 #00BFFF;
  transform: translate(-5px, -5px);
}

@media screen and (max-width:996px) {

  .intoduction h2 {
    font-size: 3rem;
  }
}

@media screen and (max-width:768px) {

  .intoduction p {
    width: 100%;
  }

  .about .learnmore {
    margin: 3rem 0 0;
  }
}

@media screen and (max-width:576px) {
  .intoduction h2 {
    font-size: 2rem;
  }

  .intoduction p {
    font-size: 14px;
  }
}
.work {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.work .title {
    font-size: 3rem;
    margin: 10px 0px 40px;
    width: 100%;
    white-space: nowrap;
    align-items: center;
    color: #ccd6f6;
    display: flex;
    align-self: center;
}

.work .title::after {
    content: "";
    display: block;
    position: relative;
    width: 300px;
    height: 1px;
    margin-left: 20px;
    background-color: #233554;
}

.box {
    width: 80%;
    margin: 3rem 0;
    opacity: 0;
    transform: translateY(50px);
    transition: opacity 0.8s ease-out, transform 0.8s ease-out;
}

.box.visible {
    opacity: 1;
    transform: translateY(0);
}

.box__inner {
    position: relative;
    height: -webkit-max-content;
    height: max-content;
}


.box__inner .img-wapper {
    position: absolute;
    width: 55%;
}

.box__inner .img-wapper-right {
    position: absolute;
    width: 55%;
    right: 0;
}

.img-wapper::after {
    content: "";
    height: 0;
    clear: both;
    overflow: hidden;
    display: block;
    visibility: hidden;
}

.box__inner a:hover {
    z-index: 11;
}

.box__inner img {
    width: 100%;
}

.box__inner img:hover {
    width: 100%;
    transform: scale(1.1);
    transition: all 0.25s cubic-bezier(0.645, 0.045, 0.355, 1);
}

.box__inner img:not( :hover) {
    transform: scale(1);
    transition: all 0.25s cubic-bezier(0.645, 0.045, 0.355, 1);
}

.box__wrapper {
    z-index: 10;
    display: flex;
    flex-direction: column;
}

.box__wrapper-right {
    align-items: flex-end;
}

.box__wrapper-left {
    align-items: flex-start;
}

.box__wrapper span {
    color: #00BFFF;
    margin: 10px 0px;
}

.box__wrapper a {
    color: #ccd6f6;
    margin: 0px 0px 20px;
    text-decoration: none;
    font-size: 2rem;
    transition: all 0.25s cubic-bezier(0.645, 0.045, 0.355, 1);
}

.box__wrapper a:hover {
    color: #00BFFF;
    text-decoration: none;
}

.box__wrapper .desc {
    background-color: #112240;
    padding: 2rem;
    width: 44%;
    z-index: 10;
    border-radius: 10px;
}

.box__wrapper .desc p {
    font-size: 16px;
    color: #a8b2d1;
}


.techstack {
    margin: 25px 0px 10px;
    display: flex;
    flex-wrap: wrap;
}

.techstack li {
    margin: 0px 20px 5px 0px;
    color: #a8b2d1;
    font-family: 'SF Mono', 'Fira Code', 'Fira Mono', 'Roboto Mono', monospace;
    font-size: 13px;
    white-space: nowrap;
}

.link a {
    display: inline-block;
    margin-right: 20px;
}

@media screen and (max-width:1000px) {
    .box__wrapper {
        display: flex;
    }

    .box__wrapper .desc {
        width: 100%;
        box-sizing: border-box;
    }

    .box__inner .img-wapper {
        position: relative;
    }

    .box__inner .img-wapper-right {
        position: relative;
    }
}

