.exp {
    display: flex;
}

.exp__sidebar {
    flex: 0 0 200px;
    padding: 20px;
}

.exp__sidebar ul {
    position: relative;
    z-index: 3;
    width: -webkit-max-content;
    width: max-content;
    padding: 0px;
    margin: 0px;
    list-style: none;
}

.exp__sidebar li {
    text-decoration: none;
    -webkit-text-decoration-skip: ink;
            text-decoration-skip-ink: auto;
    position: relative;
    transition: all 0.25s cubic-bezier(0.645, 0.045, 0.355, 1);
    display: flex;
    align-items: center;
    width: 100%;
    padding: 20px 20px;
    border-left: 2px solid #233554;
    background-color: transparent;
    color: #8892b0;
    font-family: 'SF Mono', 'Fira Code', 'Fira Mono', 'Roboto Mono', monospace;
    font-size: 12px;
    text-align: left;
    white-space: nowrap;
    cursor: pointer;
}

.exp__sidebar li.active {
    font-weight: bold;
    color: #00BFFF;
    border-left: 2px solid #00BFFF;
}


.exp__sidebar li:hover {
    color: #00BFFF;
    background-color: #112240;
}

.exp__content {
    color: #a8b2d1;
}

.exp__content h2 {
    margin-bottom: 1.5rem;
    font-size: 1.5rem;
    font-weight: 500;
    line-height: 1.3;
}

.exp__content h2 a {
    color: #00BFFF;
}

.exp__content p {
    font-size: 14px;
    margin-bottom: 1.5rem;
    font-family: 'SF Mono', 'Fira Code', 'Fira Mono', 'Roboto Mono', monospace;

}

.exp__content li {
    position: relative;
    margin-bottom: 10px;
    padding-left: 20px;
    font-size: 1rem;
    position: relative;
}

.exp__content li::before {
    content: "▹";
    position: absolute;
    left: 0px;
    color: #00BFFF;
    font-size: 12px;
    line-height: 12px;
}


.section {
    margin-bottom: 50px;
    display: none;
    transition: opacity 0.5s ease-in-out;
}

.section.visible {
    display: block;
}

