    font-style: normal;
    font-weight: 400;
    font-size: 1.2rem;
    line-height: 1rem;
    color: #ccd6f6;
    box-sizing: border-box;
}

.contact-form div {
    display: flex;
    width: 100%;
    justify-content: space-between;
    flex-wrap: wrap;
}

.contact-form div label {
    width: 48%;
    display: flex;
    flex-direction: column;
}

.contact-form textarea {
    border-radius: 4px;
    padding: 1.2rem 3.2rem;
    margin-bottom: 2rem;
    font-family: SF Pro Display, sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 1.2rem;
    line-height: 1.9rem;
    color: #ccd6f6;
    background-color: #262626;
    width: 100%;
    resize: vertical;
    box-sizing: border-box;
    box-shadow: 0 0.8rem 0.4rem rgb(51 51 51 / 25%);
    border: 1px solid #fcfcfc;
    ;
}

.contact-form .submit {
    width: 180px;
    display: block;
    color: #00BFFF;
    background-color: transparent;
    border: 1px solid #00BFFF;
    border-radius: 4px;
    padding: 1.25rem 1.75rem;
